import { defineStore } from 'pinia'

// 第一个参数必须是全局唯一，可以是哟
export const useStore = defineStore('main', {
  state: () => ({
    leftMenu: []
  }),
  getters: {
    getLeftMenu: (state) => {
      return state.leftMenu
    }
  },
  actions: {
    setLeftMenu(arr) {
      this.leftMenu = arr
    },
    clearLeftMenu() {
      this.leftMenu = []
    }
  },
})